<template>
  <yd-layout id="conlist" >

    <yd-navbar slot="navbar" :title="companItem.公司名称" bgcolor="#0FA0FA" color="#fff">
      <span  slot="left">
        <yd-navbar-back-icon color="#fff" @click.native="goback"></yd-navbar-back-icon>
      </span>
    </yd-navbar>
    <div class="linetop" slot="top">
      <span>
        <div class="topItemName">纳税总数</div>
        <div class="topItemValue">{{companyItem.companyTotalTax}}元</div>
      </span>
      <span>
        <div class="topItemName">就业总人数</div>
        <div class="topItemValue">{{companyItem.companyTotalEmployment}}人</div>
      </span>
      <span>
        <div class="topItemName">是否政府补贴</div>
        <div class="topItemValue" v-if="companyItem.companyIsSubsidies==0">否</div>
        <div class="topItemValue" v-if="companyItem.companyIsSubsidies==1">是</div>
      </span>
    </div>
    <div>
      <div v-for="(key,value) in companItem" class="companylist" >
        <div class="name">{{value}}</div>
        <div class="value">{{key}}</div>
      </div>
    </div>


  </yd-layout>
</template>

<script>
import  {mapState} from  "vuex"
export default {

  data () {
    return {
      companyname:'信息科技有限公司',
       xlineTiem:"",
      companItem:{
        "公司名称":"",
        "企业编号":"",
        "注册地址":"",
        "公司规模":"",
        "组织机构代码":"",
        "工商注册号":"",
        "公司地址":"",
        "登记机构":"",
        "注册资金":"",
        "实缴资本":"",
        "公司英文名称":"",
        "经营范围":"",
        "公司类型":"",
        "是否政府补贴":"",
        "政府补贴金额":"",
        "就业总数":"",
        "纳税总数":"",
        "经济类型":"",
        "类型层级":"",
        "经度":"",
        "维度":""
      }
    }
  },
  computed:{
    ...mapState({
      companyItem:"companyItem"
    })
  },
  watch:{

  },
  mounted: function(){
     console.log(this.companyItem);
      this.companItem={
        "公司名称":this.companyItem.companyName,
        "企业编号":this.companyItem.companyNumber,
        "注册地址":this.companyItem.companyRegisAddress,
        "公司规模":this.companyItem.companyScale,
        "组织机构代码":this.companyItem.companyOrganCode,
        "工商注册号":this.companyItem.companyRegistNumber,
        "公司地址":this.companyItem.companyAddress,
        "登记机构":this.companyItem.companyRegistAgency,
        "注册资金":this.companyItem.companyRegistCapital,
        "实缴资本":this.companyItem.companyPaidCapital,
        "公司英文名称":this.companyItem.companyEnglishName,
        "经营范围":this.companyItem.companyOperation,
        "公司类型":this.companyItem.companyType,
        "政府补贴金额":this.companyItem.companySubsidiesCapotal,
        "就业总数":this.companyItem.companyTotalEmployment,
        "纳税总数":this.companyItem.companyTotalTax,
        "经济类型":this.companyItem.companyEconomicsTypeStr,
        "类型层级":this.companyItem.companyEconomicsLevelStr,
        "经度":this.companyItem.pointLongitude,
        "维度":this.companyItem.pointLatitude
    }
  },

  methods:{
    goback:function () {
        this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
  #conlist{
    background-color: #fff;
    width: 100%;
    height: 100%;
    overflow-y: scroll;
  }
  .linetop{
    border-bottom: solid 8px #eee;
    display: flex;
    padding-top: 0.36rem;
    padding-bottom: 0.36rem;
  }
  .linetop>span{
    flex: 1;
  }
  .linetop>span .topItemName{
    line-height: 0.34rem;
    font-size: 0.24rem;
    color: rgba(125, 125, 125, 1);
    text-align: center;
  }
  .linetop>span .topItemValue{
    padding-right: 0.2rem;
    line-height: 0.76rem;
    color: rgba(16, 16, 16, 1);
    font-size: 0.46rem;
    text-align: center;
  }
  .companylist{
    border-bottom: solid 0.01rem #eee;
    padding-left: 0.32rem;
    padding-right: 0.32rem;
    padding-bottom: 0.16rem;
    padding-top: 0.16rem;
    display: flex;
    flex-direction: column;
  }
  .companylist .name{
    line-height: 0.34rem;
    color: rgba(125, 125, 125, 1);
    font-size: 0.24rem;
  }
  .companylist .value{
    line-height: 0.4rem;
    min-height: 0.4rem;
    color: rgba(57, 57, 57, 1);
    font-size: 0.28rem;
  }

</style>



